.button span::after {
  content: "";
  display: block;
  padding-left: 0.4375rem;
  width: 2.5rem;
  height: 0.5rem;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
  transform: skew(45deg);
}

.button::before {
  content: "";
  border: 1px solid #408f95;
  position: absolute;
  top: 0.3125rem;
  right: -0.3125rem;
  width: 100%;
  height: 100%;
  z-index: -2;
}

.button::after {
  background-color: #fff;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover,
.button:hover span::after {
  color: #408f95;
  border-bottom: 1px solid #408f95;
  border-right: 1px solid #408f95;
  opacity: 1;
}

.button:hover::after {
  transform: scale(1, 1);
}

