  background-color: #fff;
  border: 1px solid #fff;
  color: #408f95;
  gap: 0.875rem;
}

.button.button--color-reverse span {
  padding-right: 0.375rem;
}

.button.button--color-reverse::after {
  background-color: #408f95;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
