}

.button.button--color-reverse span::after {
  border-bottom: 1px solid #408f95;
  border-right: 1px solid #408f95;
}

.button.button--color-reverse::before {
  border: 1px solid #fff;
}

.button.button--color-reverse:hover::after {
  transform: initial;
