@use "global" as *;

.form-submit {
    position: relative;
    max-width: rem(202);
    width: 100%;
}

.form-submit::before {
    content: "";
    border: 1px solid $green-accent;
    position: absolute;
    top: rem(5);
    right: rem(-5);
    width: 100%;
    height: 100%;
    z-index: -1;
}

.form-submit::after {
    content: "";
    display: block;
    width: 40px;
    height: 8px;
    border-bottom: 1px solid #fff;
    border-right: 1px solid #fff;
    position: absolute;
    right: rem(61);
    top: 45%;
    transform: translateY(-80%) skew(45deg);
    transition: all 0.7s;
    z-index: 1;
}

.form-submit input[type="submit"] {
    position: relative;
    cursor: pointer;
    width: 100%;
    font-size: rem(14);
    padding-block: rem(16);
    padding-left: rem(16);
    padding-right: rem(78);
    background-color: $green-accent;
    font-family: $font-en2, sans-serif;
    border: 1px solid $green-accent;
    border-radius: 0;
    background-image: linear-gradient(90deg, $white 50%, transparent 50%);
    background-position: 100%;
    background-size: 250%;
    color: $white;
    text-decoration: none;
    text-transform: capitalize;
    transition: 0.3s cubic-bezier(0.45, 0, 0.55, 1);
}

.form-submit:hover input[type="submit"] {
    color: $green-accent;
    opacity: 1;
    background-position: 0;
}

.form-submit:hover::after {
    content: "";
    border-bottom: 1px solid $green-accent;
    border-right: 1px solid $green-accent;
    transition: 0.3s;
}

.form-submit button::-moz-focus-inner {
    padding: 0;
    border: none;
}
